import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import { ApicallService } from 'src/app/services/apicall.service';
import { GlobalService } from 'src/app/services/global.service';
import { ModelPage } from '../model/model.page';
import { format, parseISO } from 'date-fns';
@Component({
  selector: 'app-aproval-mtp',
  templateUrl: './aproval-mtp.page.html',
  styleUrls: ['./aproval-mtp.page.scss'],
})
export class AprovalMtpPage implements OnInit {
  public mrlist : any = ['Dr.Name1' , 'Dr.Name2' , 'Dr.Name3' , 'Dr.Name4' , 'Dr.Name5']
  public allMR : any ;
  public mtpData : any = {mr_id:'', month:'', year:''}
  public updatemtpData : any = {mr_id:'', month:'', year:'', mtp_status:''}
  public allmtpData: any;
  public allmtpstatus: any = [];
  constructor(public global:GlobalService , public apicall : ApicallService, public modalController: ModalController,) { }

  ngOnInit() {
    this.getmrData();
  }

  async getmrData(){
    await this.apicall.api_getgetActive_mr();
     this.global.GetallActive_mr.subscribe(res =>{
       this.allMR = res;
     })
   }
   async get_date(event){
    console.log(event.target.value);
    const monthformate = format(parseISO(event.target.value), 'MMMM');
    const yearformate = format(parseISO(event.target.value), 'yyyy');
    console.log(monthformate);
    console.log(yearformate);
    this.mtpData.month = monthformate;
    this.mtpData.year = yearformate;
    this.updatemtpData.month = monthformate;
    this.updatemtpData.year = yearformate;
   }
   async selectMR(event){
    console.log(event.target.value);
    this.mtpData.mr_id = event.target.value;
    this.updatemtpData.mr_id = event.target.value;
    await this.apicall.api_getmtpbydate(this.mtpData);
    this.global.MtpData.subscribe(res=>{
           console.log(res);
           this.allmtpData = res[0];
           this.allmtpstatus = res[1][0];
           console.log(this.allmtpData);
    });
   }

  async aprove(){
      this.updatemtpData.mtp_status = "approve";
      console.log(this.updatemtpData);
   await this.apicall.api_updatemtpMRstatus(this.updatemtpData);
   await this.apicall.api_getmtpbydate(this.mtpData);
   this.global.MtpData.subscribe(res=>{
          console.log(res);
          this.allmtpData = res[0];
          this.allmtpstatus = res[1][0];
          console.log(this.allmtpData);
   });
   }
  async reject(){
      this.updatemtpData.mtp_status = "reject";
      console.log(this.updatemtpData);
   await this.apicall.api_updatemtpMRstatus(this.updatemtpData);
   await this.apicall.api_getmtpbydate(this.mtpData);
   this.global.MtpData.subscribe(res=>{
          console.log(res);
          this.allmtpData = res[0];
          this.allmtpstatus = res[1][0];
          console.log(this.allmtpData);
   });
   }

   async seeProduct(mtp_id) {
    console.log(mtp_id);
    const modal = await this.modalController.create({
      component: ModelPage,
      cssClass: 'modalclass',
      componentProps: {
        mtp_id,
        "Category": "seeproduct"
      },
      initialBreakpoint: 0.5,
      breakpoints: [0.25, 0.5, 1]
    });
    return await modal.present();
  }

}
